body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: aliceblue;
    text-align: center;
}
#root{
    background-color: white;
    width: 70%;
    margin-left: 10%;
    padding: 30px;
    margin-top: 30px;
    user-select: none;
    cursor: pointer;
}
h1{
    font-size: 36px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: 900;
}
#text{
    max-width: 60%;
    min-width: 60%;
    height: 28px;
    font-size: 15px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    @extend h1;
    padding-left: 20px;
    border-radius: 5px;
   border: 2px solid #24aad6;
}
.addbtn{
    background-color: #24aad6;
    padding: 7px 20px;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 17px;
    cursor: pointer;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.addbtn:hover{
    background-color: black + 100;
    transition: .900s;
}
#model{
    background-color: rgb(255, 255, 255);
    font-size: 15px;
     padding: 7px 10px;
    @extend h1;
    width: 30%;
    margin-left: 35%;
    border: 3px solid green;
    display: none;
}
.removebtn{
   background: none;
   border: none;
   cursor: pointer;
}
img{
    width: 20px;
    height: 20px;
}
h4{
  font-size: 18px;
  @extend h1;  
  align-items: center;
  margin-right: 50px;
}
.content{
    background-color: rgb(232, 245, 249);
    margin-top: 20px;
    padding: -20px;
    width: 50%;
    height: 40px;
    margin-left: 25%;
}

@media (max-width: 800px) {
    #root{
        width: 90%;
        margin-left: 0;
    }
    .addbtn{
        margin-top: 10px;
    }
    #model{
        font-size: 13px;
        width: 80%;
        margin-left: 20px;
    }
    .content{
        width: 100%;
        margin-left: 0;
    }
}